@import "utils/vars";

.logo {
  width: $logo-width;
  height: $logo-height;
  background-image: url("../images/Logo.png");
  background-size: contain;
  background-repeat: no-repeat;
  background-position: center;
  
}

@media (max-width: 650px) {
  .logo {
    width: 74px;
    height: 48px;
    z-index: 1;
  }
}
